@import "../vars.scss";

$movil: 480px; 

.container-us {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url("./../../assets/img/usBg.svg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
    z-index: 200;
    transition: all 1s ease-in-out;
    [data-theme="dark"] & {
        background-image: url("./../../assets/img/usBgDark.svg");
        background-repeat: no-repeat;
        background-size: cover;
    }
    .title-us {
        font-family: Product;
        font-size: 24px;
        letter-spacing: 0.03em;
        color: #2a2fa9;
        margin: 59px auto;
        [data-theme="dark"] & {
            color: #fff;
        }
        @media screen and (max-width: $movil) {
            font-size: 1.3rem;
        }
    }
    .container-objectives {
        display: flex;
        width: 85%;
        justify-content: space-evenly;
        padding: 30px 40px;
        @media screen and (max-width: $movil){
            & {
                flex-direction: column;
            }
        }
        .object {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            text-align: center;
            padding: 5px;
            width: 30%;
            color: #263238;
            font-family: $roboto;
            font-size: 0.9rem;
            transition: all 1s ease-in-out;
            [data-theme="dark"] & {
                color: #fff;
            }
            @media screen and (max-width: $movil) {
                & {
                    width: 100%;
                }
            }
            div {
                background-repeat: no-repeat;
                background-position: center;
                min-height: 126px;
                transition: inherit;
                &.sale {
                    background-image: url("./../../assets/img/Wallet.svg");
                    [data-theme="dark"] & {
                        background-image: url("./../../assets/img/Wallet.svg");
                    }
                }

                &.line {
                    height: 400px;
                    background-image: url("./../../assets/img/LineDark.svg");
                    [data-theme="dark"] & {
                        background-image: url("./../../assets/img/Line.svg");
                    }
                    @media screen and (max-width: $movil) {
                        & {
                            display: none;
                        }
                    }
                }
                &.lineMov {
                    display: none;
                    @media screen and (max-width: $movil) {
                        & {
                            display: block;
                            min-height: 5px;
                            margin: 25px 0;
                            background:  linear-gradient(270deg, #77216B 17.46%, #2A2FA9 76.78%);
                            [data-theme="dark"] & {
                                background:  #fff
                            }
                        }
                    }
                }


                &.analytics {
                    background-image: url("./../../assets/img/Analytics.svg");
                    [data-theme="dark"] & {
                        background-image: url("./../../assets/img/Analytics.svg");
                    }
                }
            }
            h3 {
                font-family: Product;
                color: #77216b;
                font-size: 1.2rem;
                transition: inherit;
                [data-theme="dark"] & {
                    color: #fff;
                }
                @media screen and (max-width: $movil) {
                    & {
                        font-size: 1.4rem;
                    }
                }
            }
        }
    }
}
