@import "./../vars.scss";

$movil: 920px;

.video-section {
    position: relative;
    z-index: 10;
    transition: all 1s ease-in-out;

    [data-theme="dark"] & {
        background: #282f4a;
        color: #fff;
    }

    &::before {
        content: "";
        display: block;
        position: absolute;
        background-image: url("./../../assets/img/vectorFondo.svg");
        background-repeat: no-repeat;
        background-size: cover;
        opacity: 0.06;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 1;

        [data-theme="dark"] & {
            opacity: 0.3;
        }
    }

    .rent {
        display: flex;
        margin-left: 120px;
        padding: 30px 50px 0 50px;

        @media screen and (max-width: $movil) {
            & {
                margin-left: 0;
                padding: 0;
                flex-direction: column;
            }
        }

        .imgRent {
            background-image: url("./../../assets/img/Brainstorming.svg");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            width: 40%;

            @media screen and (max-width: $movil) {
                width: 100%;
                height: 190px;
                margin-top: 50px;
            }
        }

        .infoRent {
            width: 70%;
            padding: 20px 40px;

            @media screen and (max-width: $movil) {
                & {
                    width: 100%;
                    padding: 0;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    text-align: center;
                }
            }

            h3 {
                font-size: 1.3rem;
                font-family: $roboto;
                font-weight: 900;
                background: -webkit-linear-gradient(270deg, #5A2682 -10.45%, #462A92 24.53%, #2A2FA9 76.78%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;

                [data-theme="dark"] & {
                    background: -webkit-linear-gradient(270deg, #ffffff -10.45%, #ffffff 24.53%, #ffffff 76.78%);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                }
                @media screen and (max-width: $movil) {
                    & {
                        font-size: 1.8rem;
                    }
                }
            }

            p {
                font-size: 1.3rem;
                font-family: $roboto;
                width: 75%;

                @media screen and (max-width: $movil) {
                    & {
                        width: 90%;
                        font-size: 1.1rem;
                    }
                }
            }
        }
    }

    .video-container {
        position: relative;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
        padding: 50px 0;

        @media screen and (max-width: $movil) {
            iframe {
                width: 350px;
                height: 280px;
            }
        }
    }

    .video-text-container {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        z-index: 10;

        .video-title {
            font-family: Product;
            font-style: normal;
            font-weight: bold;
            font-size: 48px;
            letter-spacing: 0.03em;
            color: #2a2fa9;

            [data-theme="dark"] & {
                color: #fff;
            }
            @media screen and (max-width: $movil) {
                & {
                    font-size: 1.8rem;
                }
            }
        }

        .video-subTitle {
            font-family: $roboto;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            letter-spacing: 0.03em;
            color: #263238;
            width: 55%;
            text-align: center;
            padding-top: 25px;
            padding-bottom: 45px;
            [data-theme="dark"] & {
                color: #fff;
            }
            @media screen and (max-width: $movil) {
                & {
                    width: 90%;
                    font-size: 1.1rem;
                }
            }
        }
    }
}